@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/oswaldregular.woff2") format("woff2"), url("/fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/oswaldmedium.woff2") format("woff2"), url("/fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.page__body {
  min-height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}

.page__footer {
  padding: 39px 20px 22px;
}

.visually-hidden {
  position: absolute;
  transform: scale(0);
}

.page-header__nav {
  margin: 0 auto;
}
@media (max-width: 767px) {
  .page-header__nav {
    max-width: 500px;
    padding: 10px 20px 17px;
  }
}
@media (min-width: 768px) {
  .page-header__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px 17px;
  }
}
@media (min-width: 1320px) {
  .page-header__nav {
    width: 1220px;
    padding: 53px 0 29px;
  }
  .page-header__nav::before {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 720px;
    height: 688px;
    background-color: #7ec157;
    background-image: url("../img/background/promo-bg-desktop.jpg");
    content: "";
  }
}

.page-header__wrap {
  display: grid;
  grid-template-columns: 1fr 34px;
}

.page-header__logo {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  width: 100%;
  text-align: center;
}

.logo__name {
  justify-self: center;
}

.page-header__toggle {
  margin-left: 50%;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .site-menu {
    display: none;
  }
}

.site-menu {
  position: relative;
  z-index: 1;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-family: Oswald, Arial, sans-serif;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
}

.site-menu__link {
  color: #000000;
  text-decoration: none;
}
@media (min-width: 1320px) {
  .site-menu__link {
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .page-main__header {
    margin-bottom: 505px;
  }
}
@media (min-width: 1320px) {
  .page-main__header {
    margin: 0 auto;
  }
}

.page-main__process {
  margin: 0 auto;
}

.page-main__footer--dark {
  background-color: #f2f2f2;
}

.page-main__title {
  margin: 0;
  margin-top: 27px;
  margin-left: 20px;
  margin-bottom: 41px;
  font-family: Oswald, Arial, sans-serif;
  font-size: 36px;
  line-height: 1;
  font-weight: 400;
}

.page-main__subtitle {
  margin: 0 20px 20px;
  font-family: Oswald, Arial, sans-serif;
  text-transform: uppercase;
}

.page-main__heading {
  margin: 0;
  margin-bottom: 40px;
  font-family: Oswald, Arial, sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}
@media (min-width: 768px) {
  .page-main__heading {
    margin-bottom: 63px;
    font-size: 60px;
    line-height: 1;
  }
}
@media (min-width: 1320px) {
  .page-main__heading--example-height {
    margin-top: 43px;
  }
}

.promo {
  position: relative;
  padding: 27px 20px 0;
  font-family: Oswald, Arial, sans-serif;
  line-height: 1;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  background-image: url("../img/background/promo-bg-mobile.jpg"), linear-gradient(to bottom, #7ec157 60%, #ffffff 50%);
  background-repeat: repeat-x;
  background-position: top center;
}
@media (min-width: 768px) {
  .promo {
    padding: 62px 117px 0;
    color: #000000;
    text-align: left;
    background-image: none;
    z-index: 1;
  }
}
@media (min-width: 1320px) {
  .promo {
    width: 1220px;
    padding-bottom: 185px;
  }
}

.promo__heading {
  margin: 0;
  margin-bottom: 25px;
  font-weight: 400;
  font-size: 36px;
}
@media (min-width: 768px) {
  .promo__heading {
    margin-bottom: 41px;
    font-size: 60px;
  }
}

.promo__text {
  margin: 0;
  margin-bottom: 29px;
  font-size: 14px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__text {
    margin-bottom: 49px;
    font-size: 20px;
  }
}
@media (min-width: 1320px) {
  .promo__text {
    margin-bottom: 52px;
  }
}

@media (min-width: 768px) {
  .promo__img {
    position: absolute;
    left: 270px;
    bottom: -370px;
    transform: scale(220%);
    z-index: -1;
  }
}
@media (min-width: 1320px) {
  .promo__img {
    top: 100px;
    left: 600px;
    transform: scale(185%);
  }
}

@media (max-width: 767px) {
  .promo__button {
    max-width: 500px;
    margin: 0 auto;
    padding: 13px 25px;
  }
}
@media (min-width: 768px) {
  .promo__button {
    width: 244px;
    padding: 17px 25px;
  }
}

.button {
  display: block;
  border: none;
  font-family: Oswald, Arial, sans-serif;
  font-size: 14px;
  line-height: 1;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #68B738;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
  }
}

.catalog-sections {
  margin: 0 auto;
}
@media (max-width: 767px) {
  .catalog-sections {
    max-width: 540px;
    padding: 20px;
  }
}
@media (min-width: 768px) {
  .catalog-sections {
    padding: 45px 30px;
  }
}
@media (min-width: 1320px) {
  .catalog-sections {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;
    width: 1220px;
    padding: 82px 0 71px;
  }
}

.catalog-sections__block:not(:last-child) {
  margin-bottom: 20px;
}
@media (min-width: 1320px) {
  .catalog-sections__block:not(:last-child) {
    margin-bottom: 0;
  }
}

.catalog-block {
  margin: 0 auto;
  padding: 21px 22px 18px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .catalog-block {
    max-width: 945px;
    padding: 77px 264px 53px 53px;
  }
}
@media (min-width: 1320px) {
  .catalog-block {
    width: 570px;
    padding: 77px 52px 59px;
  }
}

.catalog-block__heading {
  margin: 0;
  margin-bottom: 29px;
  font-family: Oswald, Arial, sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .catalog-block__heading {
    font-size: 36px;
    line-height: 1;
  }
}
@media (min-width: 1320px) {
  .catalog-block__heading {
    margin-bottom: 73px;
    text-align: center;
  }
}

.catalog-block__text {
  margin-bottom: 15px;
  padding-bottom: 23px;
}
@media (min-width: 768px) {
  .catalog-block__text {
    margin-right: 54px;
    margin-bottom: 48px;
    padding-bottom: 0;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1320px) {
  .catalog-block__text {
    margin-right: 0;
    margin-bottom: 23px;
  }
}

.catalog-block__link {
  font-family: Oswald, Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: #000000;
}

@media (max-width: 767px) {
  .process {
    max-width: 540px;
    padding: 0 20px 20px;
  }
}
@media (min-width: 768px) {
  .process {
    max-width: 945px;
    padding: 0 30px 47px;
  }
}
@media (min-width: 1320px) {
  .process {
    max-width: 1220px;
    padding: 0 0 47px;
  }
}

.process__list {
  margin: 0;
  padding: 0;
  color: #444444;
  list-style-type: none;
}
@media (min-width: 768px) {
  .process__list {
    display: grid;
    grid-template-columns: repeat(2, 230px);
    gap: 37px 153px;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1320px) {
  .process__list {
    grid-template-columns: repeat(4, 230px);
    gap: 100px;
  }
}

.process__item {
  outline: 1px solid black;
}
@media (max-width: 767px) {
  .process__item {
    margin-bottom: 20px;
    padding-left: 80px;
  }
}
@media (min-width: 768px) {
  .process__item {
    width: 230px;
    margin-bottom: 0;
    padding: 111px 0 57px;
  }
}

@media (max-width: 767px) {
  .example {
    padding: 25px 20px 40px;
    background-color: #f2f2f2;
  }
}
@media (min-width: 768px) {
  .example {
    padding: 43px 33px 40px;
    background-image: linear-gradient(to top, #eaeaea 40%, #ffffff 40%);
  }
}
@media (min-width: 1320px) {
  .example {
    padding: 0;
    padding-bottom: 75px;
    background-image: linear-gradient(to top, #eaeaea 70%, #ffffff 60%);
  }
}

.example__wrap {
  margin: 0 auto;
}
@media (max-width: 767px) {
  .example__wrap {
    max-width: 500px;
  }
}
@media (min-width: 768px) {
  .example__wrap {
    max-width: 940px;
  }
}
@media (min-width: 1320px) {
  .example__wrap {
    display: grid;
    grid-template-columns: 436px 591px;
    justify-content: space-between;
    max-width: 1220px;
  }
}

.example__text {
  margin: 0;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .example__text {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1320px) {
  .example__text {
    margin-top: 83px;
    margin-bottom: 69px;
  }
}

@media (min-width: 768px) {
  .example__numbers-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    max-width: 850px;
    margin-bottom: 80px;
  }
}
@media (min-width: 1320px) {
  .example__numbers-wrap {
    grid-row: 3/5;
    margin-bottom: 0;
  }
}

.example__list {
  margin: 0;
}
@media (max-width: 767px) {
  .example__list {
    display: grid;
    grid-template-columns: repeat(2, 124px);
    justify-content: space-between;
    max-width: 320px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
}
@media (min-width: 768px) {
  .example__list {
    display: grid;
    grid-template-columns: repeat(2, 162px);
    justify-content: space-between;
    width: 366px;
  }
}
@media (min-width: 1320px) {
  .example__list {
    margin-bottom: 55px;
  }
}

.example__item {
  text-align: center;
}

.example__number {
  font-family: Oswald, Arial, sans-serif;
  font-size: 24px;
  line-height: 1;
}
@media (min-width: 768px) {
  .example__number {
    font-size: 30px;
    line-height: 37px;
  }
}

.example__desc {
  margin: 0;
  font-size: 12px;
  line-height: 1;
}
@media (min-width: 768px) {
  .example__desc {
    font-size: 14px;
  }
}

.example__cost {
  margin: 0;
  font-family: Oswald, Arial, sans-serif;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .example__cost {
    display: grid;
    font-size: 20px;
    text-align: right;
  }
}
@media (min-width: 1320px) {
  .example__cost {
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
  }
}

.example__images {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 15px;
}
@media (min-width: 1320px) {
  .example__images {
    grid-row: 1/4;
    grid-column: 2/3;
    margin-bottom: 0;
  }
}

.example__switcher {
  margin: 0 auto;
}
@media (min-width: 1320px) {
  .example__switcher {
    grid-column: 2/3;
  }
}

.switcher {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .switcher {
    width: 210px;
  }
}
@media (min-width: 768px) {
  .switcher {
    width: 591px;
  }
}

@media (min-width: 1320px) {
  .contacts {
    display: grid;
    grid-template-columns: 110px 565px 1fr;
    grid-template-rows: 95px auto 107px;
  }
}

.contacts__wrap {
  display: flex;
  justify-content: space-between;
  max-width: 540px;
  margin: 0 auto;
  padding: 26px 20px;
  font-size: 16px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .contacts__wrap {
    max-width: 768px;
    padding: 60px 70px 56px;
  }
}
@media (min-width: 1320px) {
  .contacts__wrap {
    grid-column: 2/3;
    grid-row: 2/3;
    width: 565px;
    padding: 56px 80px;
    background-color: #ffffff;
    z-index: 1;
  }
}

.contacts__heading {
  font-family: Oswald, Arial, sans-serif;
  line-height: 20px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .contacts__heading {
    font-size: 20px;
    line-height: 26px;
  }
}

.contacts__address {
  font-size: 14px;
  line-height: 20px;
  color: #444444;
  font-style: normal;
}
@media (min-width: 768px) {
  .contacts__address {
    display: grid;
    align-content: space-between;
    font-size: 16px;
    text-align: right;
  }
}
@media (min-width: 1320px) {
  .contacts__address {
    width: 565px;
  }
}

.contacts p {
  margin: 0;
}

.contacts__img {
  display: block;
}
@media (min-width: 1320px) {
  .contacts__img {
    grid-column: 1/-1;
  }
}

.products__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.products__item {
  padding: 20px;
  padding-bottom: 25px;
}

.product-card__name {
  margin: 0;
  font-family: Oswald, Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #222222;
  text-transform: uppercase;
}

.product-card__info-list {
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  color: #444444;
}

.show-all {
  text-align: center;
}

.show-all__name {
  margin: 0;
  margin-bottom: 8px;
  font-family: Oswald, Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #111111;
  text-transform: uppercase;
}

.show-all__text {
  margin: 0;
  margin-bottom: 21px;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.show-all__button {
  color: #444444;
  background-color: #f2f2f2;
}

.extra-products {
  padding: 25px 20px 35px;
}

.extra-products__title {
  margin: 0;
  margin-bottom: 36px;
  font-family: Oswald, Arial, sans-serif;
  font-size: 24px;
  line-height: 1;
  font-weight: 400;
}

.extra-products__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.extra-products__item {
  padding-top: 13px;
  padding-bottom: 19px;
}

.extra-card__name {
  margin: 0;
  margin-bottom: 13px;
  font-family: Oswald, Arial, sans-serif;
  font-family: 16px;
  line-height: 1;
  font-weight: 400;
  color: #222222;
  text-transform: uppercase;
}

.extra-card__list {
  margin: 0;
  margin-bottom: 15px;
}

.extra-card__definition {
  margin: 0;
  line-height: 1;
  color: #444444;
}

.extra-products__offer {
  padding-top: 35px;
}

.offer {
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}

.offer__text {
  height: 289px;
  margin: 0;
  background-color: #68B738;
  background-image: url("../img/background/offer-bg-mobile.jpg");
}

.form {
  padding-bottom: 32px;
}

.form__group {
  padding: 0;
  border: none;
}
.form__group--personal {
  padding: 20px 20px 24px;
}
.form__group--program {
  margin-bottom: 26px;
  padding: 22px 20px 19px;
}
.form__group--owner-contacts {
  margin-bottom: 24px;
  padding: 0 20px;
}
.form__group--comments {
  margin-bottom: 23px;
  padding: 0 20px;
}
.form__group--additional {
  margin-bottom: 34px;
  padding: 0 20px;
}

.form__button {
  margin: 0 20px 21px;
}

.form__required-text {
  margin: 0;
  line-height: 16px;
  text-align: center;
}

.field-group__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.field-group__heading {
  display: block;
  margin-bottom: 35px;
  font-family: Oswald, Arial, sans-serif;
  font-size: 24px;
  line-height: 26px;
}

.field__label,
.field__input::placeholder {
  font-family: Oswald, Arial, sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  color: #444444;
}

.field__input {
  padding: 13px 16px;
}

.control__label {
  font-family: Oswald, Arial, sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  color: #444444;
}

.page-footer {
  background-color: #f2f2f2;
}

.page-footer__logo {
  display: block;
  margin-bottom: 32px;
}

.page-footer__socials {
  margin: 0;
  margin-bottom: 22px;
  padding: 0;
}

.socials {
  padding: 20px 0;
  list-style-type: none;
}

.socials__link {
  display: block;
  width: 16px;
  height: 16px;
  outline: 1px solid tomato;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}